import styled from 'styled-components'

export const ImageWrapper = styled.div`
        border-radius: 5px;
        max-width: 100%;
        height: auto;
        margin-bottom: 20px;
        
        img {
               border-radius: 5px;
               box - shadow: 0 1 px 5 px rgba(0, 0, 0, 0.46);
        }
`